import {
  getCommentsFromArticle,
  addCommentsToArticle,
  deleteComment,
} from "@/api/article";
import { mapState } from "vuex";
export default {
  name: "ArticleComment",
  props: {
    article: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      comments: [],
      comment: {
        body: "",
      },
      addDisabled: false,
      deleteDisabled: false,
      errors: []
    };
  },
  computed: {
    ...mapState(["user"]),
  },
  mounted() {
    this.getComments()
  },
  methods: {
    async getComments() {
      try {
        const { data } = await getCommentsFromArticle(this.article.slug);
        this.comments = data.comments;
      } catch (err) {
        
        this.errors = err.message;
      }
    },
    async onSubmit() {
      if (this.comment.body == '') {
        this.errors.push('评论不能为空') 
        return
      }
      if (this.addDisabled) return;
      this.addDisabled = true;
      try {
        await addCommentsToArticle(this.article.slug, this.comment);
        this.comment.body = "";
        this.getComments();
      } catch (err) {
        this.errors = err.message;
      }
      this.addDisabled = false;
      this.errors = []
    },
    async deleteComment(id) {
      if (this.deleteDisabled) return;
      this.deleteDisabled = true;
      try {
        await deleteComment(this.article.slug, id);
        this.getComments();
      } catch (err) {
        this.errors = err.message;
      }
      this.deleteDisabled = false;
    },
  },
};
