import { deleteArticle } from '@/api/article'
import { mapState } from 'vuex'
export default {
  name: "ArticleMeta",
  props: {
    article: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      errors: '',
      deleteDisabled: false
    }
  },
  computed: {
    ...mapState(['user'])
  },
  methods: {
    async deleteArticle() {
      this.deleteDisabled = true
      try {
        await deleteArticle(this.article.slug)
        this.$router.push('/')
      } catch(err) {
        this.errors = err.message
      }
      this.deleteDisabled = false
    }
  }
};
